const textFieldStyles = makeStyles({
	root: {
		'& > div': {
			backgroundColor: 'transparent',
		}, 
		'& .Mui-disabled': {
			color: "#272E4C"
		}	
	}
});

const datePickerStyles = makeStyles({
	keyboardDatePicker: {
		// marginTop: '10px',
		// marginBottom: '20px',
		width: '100%',
	},
});
const Select = ({ items, ...restProps  }: any) => {
	return (
		<MaterialUI.Select
			variant='outlined'
			style={{ width: '100%' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.label;
					} else {
						return '——';
					}
				}
				return '——';
			}}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem key={i} value={item.value}>
					{item.label}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};
const StatusItems = [
	{ label: '', value: '' },
	{ label: 'Closed', value: 'Active' },
	{ label: 'Approved', value: 'Approved' },
	{ label: 'Disapproved', value: 'Disapproved' },
	{ label: 'For Approval', value: 'For Approval' },
	{ label: 'Signed', value: 'Signed' },
	{ label: 'For Revision', value: 'For Revision' },
	{ label: 'Submitted', value: 'Submitted' },
	{ label: 'Resubmitted', value: 'Resubmitted' },
	{ label: 'Expired', value: 'Expired' },
	{ label: 'Awaiting Activation', value: 'Awaiting Activation' },
];

