  const handleActionButton = (status:string
    ,proposalID:string
    ,clientName:string
    ,clientID:string
    ,ticketID:string
    ,fileArray:any[]
    ) => {
    console.log(status,proposalID,clientID,ticketID);
    setProposalToDownload(proposalID);
    setRegisteredNametoDownload(clientName);
    setClientIDtoDownload(clientID)
    switch(status){
      case"Submitted":
      case"Resubmitted":
        setRenderAction('view')
        setRenderType('renderProposal')
        setDownloadProposal(true)
      break;
      case'Expired':
      case'Awaiting Activation':
      case"Active":
        setRedirectType('active')
        setRedirect(true)
        /*setRenderAction('view')
        setRenderType('renderContract')
        setDownloadProposal(true)*/
      break;
      case"Approved":
        setRenderAction('download')
        setRenderType('renderProposal')
        setDownloadProposal(true)
        setLoading(true)
      break;
      case"Signed":
        setLoading(true);
        handleSignedAction(fileArray); 
      break;
      case"For Revision":
        if(ticketID)
        {setProposalToDownload(ticketID);}
        setRedirectType('revision')
        setRedirect(true)
      break;
      case"Disapproved":
        setRedirectType('disapprove')
        setRedirect(true)
      break;

    }
  }

    const handleSignedAction = (files:any[]) => {
      /*files.forEach(item=>{
        console.log('Action Button',item)

      })*/
      for (let file of files) {
        //console.log(file);
        if (file['url']) {
            file['url'] = file['url'].replace('public/', API_URL)
            let a = document.createElement('a');
            a.href = file['url'];
            a.download = file['name'];
            a.click();
            setLoading(false);
        }
      }
    }
