import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
    Grid, IconButton} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { green } from '@material-ui/core/colors';
// Library of PDFJS
// import * as pdfjsLib from "pdfjs-dist";
// import * as pdfjsViewer from 'pdfjs-dist/web/pdf_viewer';

interface RenderPdfModalProps {
    isModalOpen: boolean;
    id: string;
    pdfUrl: string;
    onClose: () => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'100%',
        fontFamily: 'usual',
    },
    dialogContent :{
        padding: '0px',
        marginTop:'0px',
        justify:'center',
        overflow:'scroll',
        textAlign: 'center'
    },
    dialogContentText :{
        fontSize: '0.875rem',
    },
    item: {
        width: '100%',
        paddingTop: '0px',
        minHeight: '730px'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '100%',
        minHeight: '500px'
    },
    iframe: {
        width: '90%',
        height: '85%',
        top: 50,
        left: 50,
        bottom: 50,
        right: 50,
    },
    dialogTitle: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        paddingLeft: '0px',
        fontSize: '16px',
        color: '#272E4C'
    },
    dialogAction: {
        marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
        justifyContent:'center',
    },
    boldText:{
        fontWeight: 'bold'
    },
    cancelButton: {
        width:115,
        marginRight:25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    submitButton: {
        width:115,
        marginLeft:25,
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    loadingIndicator: {
        marginTop: theme.spacing(3),
        width: '100%',
        color: green[500],
        justify: 'center'
    }
}));
  

export const RenderPdfModal: React.FC<RenderPdfModalProps> = (
	props: RenderPdfModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;
    const classes = useStyles();
    let viewerUrl = `${process.execPath}/../pdfjs-2.2.228-dist/web/viewer.html?file=${props.pdfUrl}`;
    // const viewerUrl = `https://mozilla.github.io/pdf.js/web/viewer.html?file=${props.pdfUrl}`;

	const handleClose = () => {
		props.onClose();
	};

    return (
        <Dialog
            id={id}
            open={isModalOpen}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
            fullWidth={true}
            fullScreen={true}
            maxWidth = {'md'}
        >
            <div>
                <Grid container className={classes.container} justify="space-between" alignItems="flex-start" direction="column">
                    <Grid item>
                        <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                            <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                    </Grid>
                    <Grid container justify="center" alignItems="stretch" alignContent="stretch">
                        <DialogContent className={classes.dialogContent}>
                            <Grid container justify="center" alignItems="stretch" alignContent="stretch">
								<Grid item xs={12} className={classes.item}>
                                    <iframe src={viewerUrl} className={classes.iframe}>
                                    </iframe>
								</Grid>
							</Grid>
                        </DialogContent>
                    </Grid>
                </Grid>
            </div>
        </Dialog>
    )
}

RenderPdfModal.defaultProps = {
    isModalOpen: false,
    id: 'render_pdf_modal',
    pdfUrl: '',
};
